<template>
  <div class="popup_wrapper" ref="popup_wrapper">
    <div class="v-popup">
      <div class="v-popup__header">
        <span></span>
        <span>
          <i
            class="material-icons"
            @click="closePopup"
          >
          close
          </i>
        </span>
      </div>
      <div class="v-popup__content">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'v-popup',
    props: {
      popupTitle: {
        type: String,
        default: 'Popup name'
      },
      addCart: {
        type: String,
        default: "Popup add"
      }
    },
    data() {
      return {

      }
    },
    computed: {},
    methods: {
      addCartAction() {
        this.$emit('addCartAction')
      },
      closePopup() {
        this.$emit('closePopup')
      }
    },
    mounted() {
      let vm = this;
      document.addEventListener('click', function(item) {
        if(item.target === vm.$refs['popup_wrapper']) {
          vm.closePopup()
        }
      })
    }
  }
</script>

<style lang="scss">
  .popup_wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(64, 64, 64, .6);
    z-index: 1250;
   

    .v-popup {
      padding: 16px;
      position: absolute;
      width: 60%;
      min-width: 1000px;
      top: 50px;


      background: #fff;
      &__header, &__footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &__content {
        display: flex;
        justify-content: center;
        align-items: center;

      }
      .submit_btn {
        padding: 8px;
        color: #fff;
        background-color: #26ae68;
      }
      .close_modal {
        padding: 8px;
        color: #fff;
        background-color: #575757;
      }
      @media (max-width: 1100px) {  
        overflow-x: hidden;
        padding: 16px;
        position: fixed;
        width: 100%;
        min-width: 300px;
        height: 100vh;
        top: 0;
      }
      @media (max-width: 599px) {
        &__content {
          align-items: flex-start;
        }
      }
    }
  }

  
</style>