<template>
  <div class="products__item">

    <vPopup
      v-if="isInfoPopupVisible"
      @closePopup="closePopup"
      addCart="Добавить в корзину"
      :popupTitle="product_data.title"
      @addCartAction="addToCart"
    >
      <div class="popup-item">
        <img class="popup-item__image" :src="require('../app/images/' + product_data.image)" alt="">
        <div class="popup-item__info">
          <div class="popup-item__honey-info">
            <h2 class="popup-item__title">{{product_data.title}}</h2>
            <p class="popup-item__instock">В наличии</p>
            <div class="popup-item__product-info">
              <p class="popup-item__discript">Описание того, каким Божественным образом height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;был выращен данный мёдheight: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;height: 100vh;</p>
              <p class="popup-item__composition">Состав: Мёд, грецкий орех, пот Святого Игнатия</p>
            </div>
          </div>
          <div class="popup-item__price-section">
            <p class="popup-item__price">{{product_data.discount}} руб <span class="popup-item__old-price">{{product_data.price}} руб</span></p>
            <p class="popup-item__weight">{{product_data.weight}} гр</p>
            <button class="popup-item__button-discript" @click="addToCart">В корзину</button>
          </div>
        </div>
      </div>
    </vPopup>
    
    <img class="product--image" :src="require('../app/images/' + product_data.image)" alt="">
    <span class="product__weight">{{product_data.weight}} гр.</span>
    <p class="product__info">{{product_data.title}}</p>
    <p class="product__info">&#8381; {{product_data.discount}} <span class="old-price">&#8381; {{product_data.price}}</span></p>
    <button class="products__button" @click="addToCart">В корзину</button>
    <button class="products__button more" @click="showPopupInfo">Подробнее</button>
  </div>
</template>

<script>
import vPopup from './popup/v-popup'
export default {
  name: 'catalog-item',
  data() {
    return {
      isInfoPopupVisible: false
    }
  },
  props: {
    product_data: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  methods: {
    addToCart() {
      this.$emit('addToCart', this.product_data)
    },
    showPopupInfo() {
      this.isInfoPopupVisible = true
      this.blockBodyScroll()
    },
    closePopup() {
      this.isInfoPopupVisible = false
      this.blockBodyScroll()
    },
    blockBodyScroll() {
      let gHtml = document.querySelector('html')
      if(this.isInfoPopupVisible) {
        gHtml.style.overflowY = "hidden"
      }
      else {
        gHtml.style.overflowY = "scroll"
      }
    }
  },
  components: {
    vPopup
  }
}

</script>

<style lang="scss">
  .popup-item {
    font-family: "PT Sans";
    display: flex;
    justify-content: center;
    align-items: flex-start;

    padding: 20px 30px 20px 0px;
    margin-bottom: 40px;
    /* Блок с описанием товара*/
    &__info {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: row;

    }
    &__honey-info {
      display: flex;
      flex-direction: column;
      margin: 10px 0;

      max-width: 450px;
    }
    /*Фото товара*/
    &__image {
      width: 300px;
      margin-right: 30px;

    }
    /*Название товара*/
    &__title {
      font-size: 36px;
      font-weight: 400;
      margin: 10px 0;
    }
    /* Наличие товара */
    &__instock {
      margin: 10px 0;
    }
    /*================ 1-ый блок ================*/
    &__product-info {
      justify-content: center;
    }
    /* Описание товара */
    &__discript {
      font-size: 18px;
    }
    /* Состав товара */
    &__composition {
      font-size: 14px;
    }
    /*================ 2-ой блок ================*/
    &__price-section {
      display: flex;
      flex-direction: column;
      position: relative;
      align-items: center;
      width: 40%;
    }
    /* старая цена */
    &__old-price {
      text-decoration: line-through;
      color: #bdbdbd;
      font-size: 18px;
      padding: 0 15px;
    }
    /* цена товара */
    &__price {
      font-weight: bold;
      font-size: 24px;
      margin-bottom: 0;
    }
    /* вес товара */
    &__weight {
      font-size: 12px;
    }
    /*персонализация кнопки*/
    &__button-discript {
      background-color: #fbd765;
      width: 85%;
      height: 62px;
      border: 0;
      font-size: 14px;
    }
    &__button-discript:focus {
      outline: none;
    }
    &__button-discript:active {
      transform: scale(0.95);
    }
    @media (max-width: 1100px) {
      .popup-item {

        &__info {
        width: 100%;
        display: flex;

        align-items: flex-start;
        flex-direction: column;

        }
        &__honey-info {
        display: flex;
        flex-direction: column;

        }
        &__image {
          width: 30%;

        }
        &__price-section {
          display: flex;
          flex-direction: column;
          position: relative;
          align-items: center;
          width: 100%;
          

        }
      }
    }
    @media (max-width: 599px) {
     
      padding: 0;
      flex-direction: column;
      .popup-item {
        
        &__image {
          display: none;
        }
        &__honey-info {
          
        }
        &__price-section {
          
        }
      }
    }
  }
</style>