<template>
    <div class="catalog section-box--mt" id="catalog">
      <div class="container container--center header-bottom">
        <h1 class="header-section">Каталог</h1>
      </div>
      <div class="products">
        
        <CatalogItem 
          v-for="product in PRODUCTS"
          :key="product.id"
          :product_data="product"
          @addToCart="addToCart"
        />

      </div>
      <div class="container container--center">
        <button class="button-all">
          Все товары
        </button>
      </div>
    </div>
</template>
<script>
  import CatalogItem from './Catalog-item'
  import {mapGetters, mapActions} from 'vuex'
  
  export default {
    name: 'catalog',
    components: {
      CatalogItem
    },
    methods: {
      ...mapActions(['GET_PRODUCTS_FROM_API', 'ADD_TO_CART']),
      addToCart(data) {
        this.ADD_TO_CART(data)
      }
    },
    computed: {
      ...mapGetters(['PRODUCTS', 'CART'])
    },
    mounted() {
      this.GET_PRODUCTS_FROM_API()
    },
  }
</script>